@import '../common/variables.scss';

.owl-carousel {
  display: block;
  width: 100%;
  height: auto;
}

.owl-nav {
  display: none;
}

.owl-dots {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 10px;
}

.owl-carousel > .owl-dots > .owl-dot {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: #bec2c7;
  margin-right: 20px;

  &:hover {
    background-color: #7e868f;
  }

  &:focus {
    background-color: #e9ebec;
  }
}

.owl-dots > .owl-dot.active {
  background-color: #fff;
  border: 3px solid #283645;
  width: 10px;
  height: 10px;
}

@media (min-width: $tablet-width) {
  .owl-dots {
    margin-top: 5%;
  }
}

@media (min-width: $desktop-width) {
  .owl-dots {
    display: none;
  }

  .owl-nav {
    display: block;
  }

  .reviews__wrapper > .owl-carousel .owl-nav button.owl-prev {
    background-image: url(./../images/left-arrow.png);
    background-repeat: no-repeat;
    position: absolute;
    display: block;
    width: 46px;
    height: 30px;
    font-size: 0;
    background-color: #fff;
    background-repeat: no-repeat;
    background-position: center;
    outline: 0;
    cursor: pointer;
    top: 30%;
    left: 0;

    &:hover {
      background-image: url(./../images/left-arrow-hover.png);
    }

    &:focus {
      background-image: url(./../images/left-arrow-focus.png);
    }
  }

  .reviews__wrapper > .owl-carousel .owl-nav button.owl-next {
    background-image: url(./../images/right-arrow.png);
    background-repeat: no-repeat;
    position: absolute;
    display: block;
    width: 46px;
    height: 30px;
    font-size: 0;
    background-color: #fff;
    background-repeat: no-repeat;
    background-position: center;
    outline: 0;
    cursor: pointer;
    top: 30%;
    right: 0;

    &:hover {
      background-image: url(./../images/right-arrow-hover.png);
    }

    &:focus {
      background-image: url(./../images/right-arrow-focus.png);
    }
  }
}
